.container {
  display: flex;
  flex-direction: column;
  padding: 20px;
  justify-content: center;
  align-items: center;
}
.contactForm {
    display: flex;
    flex-direction: column;
    gap: 10px;
     border: 5px solid#1976D2;
     border-radius: 10px;
     padding: 20px;
     width: 400px;
}

.label {
display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 18px;
  font-weight: 600;
  color: #1976D2;
}
.contactInput {
 padding: 5px;
  border: 1px solid #1976D2;
  border-radius: 5px;
  font-size: 12px;
  color: #1976D2;
  background-color: transparent;
}
.contactInput:hover {
  border: 1px solid #134e8a;
  background-color: #E8F0FE;
}
.contactInput::placeholder {
  color: #1976D2;
}

.addContactBttn {
    width: 100px;
    padding: 10px;
    cursor: pointer;
    background-color: #1976D2;
    border: 1px solid #1976D2;
    color: antiquewhite;
    border-radius: 5px;
    transition: all 0.3s ease;
}
.addContactBttn:hover {
    background-color: rgb(52, 108, 198);
}
.addContactBttn:focus {
    background-color: rgb(52, 108, 198);
}